








































import Vue from 'vue'
// @ts-ignore
import VueHeadful from 'vue-headful'
import { mapState } from 'vuex'

// Components
import BaseEventCard from '@/components/BaseEventCard'
// import TheCreateEventForm from '@/components/TheCreateEventForm'

// Store actions
import { FETCH_EVENTS } from '@/store/modules/events/action-types'

// Utils
import { titleEnhancer } from '../utils'


export default Vue.extend({
    name: 'Home',

    components: {
        BaseEventCard,
        // TheCreateEventForm,
        VueHeadful,
    },

    data: (): any => ({
        isCreateDialogShown: false,
    }),

    computed: {
        ...mapState({
            // @ts-ignore
            events: state => state.events.items,
            // @ts-ignore
            isLoading: state => state.isLoading,
        }),

        currentRouteTitle(): string {
            return titleEnhancer('Главная')
        },
    },

    // methods: {
    //     async handleCreateEvent(): Promise<void> {
    //         await this.$store.dispatch(FETCH_EVENTS)
    //     },
    // },

    async created() {
        await this.$store.dispatch(FETCH_EVENTS)
    },
})
