
















































import Vue from 'vue'
import { mapState } from 'vuex'
import moment from 'moment'

// Store
import { GET_CHAINED_EVENTS } from '@/store/modules/events/getter-types'

// Route names
import { EVENT_DETAILS_ROUTE_NAME } from '@/router/route-names'


export default Vue.extend({
    name: 'BaseEventsChain',

    props: {
        // Идентификатор конфликта
        conflictId: {
            type: Number,
            required: true,
        },

        // Заголовок
        title: {
            type: String || null,
            required: false,
            default: null,
        },
    },

    computed: {
        ...mapState({
            // @ts-ignore
            eventTypes: state => state.references.eventTypes,
        }),

        chainedEvents(): Array<any> {
            return this.$store.getters[GET_CHAINED_EVENTS](this.$props.conflictId)
        },

        selectedEventId(): number {
            return Number(this.$store.state.route.params.id)
        },
    },

    methods: {
        formatDate(date: number): string {
            return moment(date).format('D MMMM YYYY')
        },

        onChainedEventClick(eventId: number): void {
            if (this.selectedEventId !== eventId) {
                this.$router.push({
                    name: EVENT_DETAILS_ROUTE_NAME,
                    params: {
                        id: eventId,
                    },
                } as any)
            }
        },

        getEventTypeName(typeId: number): string {
            // @ts-ignore
            return this.eventTypes.find(
                (type: any) => type.id === typeId,
            ).name
        },
    },
})
