



























































import Vue from 'vue'
import { mapState } from 'vuex'
import moment from 'moment'

// Route names
import { EVENT_DETAILS_ROUTE_NAME } from '@/router/route-names'


export default Vue.extend({
    name: 'BaseEventCard',

    props: {
        // Данные о событии
        eventData: {
            type: Object,
            required: true,
        },
    },

    computed: {
        ...mapState({
            // @ts-ignore
            eventStatuses: state => state.references.eventStatuses,
            // @ts-ignore
            eventTypes: state => state.references.eventTypes,
            // @ts-ignore
            regions: state => state.references.regions,
        }),

        eventStatusName(): string {
            // @ts-ignore
            return this.eventStatuses.find(
                (status: any) => status.id === this.$props.eventData.event_status_id,
            ).name
        },

        eventTypeName(): string {
            // @ts-ignore
            return this.eventTypes.find(
                (type: any) => type.id === this.$props.eventData.event_type_id,
            ).name
        },
    },

    methods: {
        formatDate(date: string): string {
            return moment(date).format('D MMMM YYYY')
        },

        onCardClick(): void {
            this.$router.push({
                name: EVENT_DETAILS_ROUTE_NAME,
                params: {
                    id: this.$props.eventData.id,
                },
            })
        },

        getRegionName(regionId: number): string {
            // @ts-ignore
            return this.regions.find(region => region.id === regionId).name
        },
    },
})
