









































































































import Vue from 'vue'
import { mapState } from 'vuex'
import moment from 'moment'

// Icons
// @ts-ignore
import CommentsIcon from '@/assets/icons/comments.svg'
// @ts-ignore
import LogoVk from '@/assets/icons/logo-vk.svg'
// @ts-ignore
import LogoTwitter from '@/assets/icons/logo-twitter.svg'
// @ts-ignore
import SourceIcon from '@/assets/icons/source.svg'


export default Vue.extend({
    name: 'BaseEventInfo',

    components: {
        CommentsIcon,
        LogoVk,
        LogoTwitter,
        SourceIcon,
    },

    props: {
        // Объект с данными о конфликте
        eventData: {
            type: Object,
            required: true,
        },
    },

    computed: {
        ...mapState({
            // @ts-ignore
            eventStatuses: state => state.references.eventStatuses,
            // @ts-ignore
            eventTypes: state => state.references.eventTypes,
        }),

        eventStatusName(): string {
            // @ts-ignore
            return this.eventStatuses.find(
                (status: any) => status.id === this.$props.eventData.event_status_id,
            ).name
        },

        eventTypeName(): string {
            // @ts-ignore
            return this.eventTypes.find(
                (type: any) => type.id === this.$props.eventData.event_type_id,
            ).name
        },

        formattedDate(): string {
            return moment(this.$props.eventData.date).format('DD.MM.YYYY')
        },

        commentsCount(): number {
            return this.$props.eventData.comments
                ? this.$props.eventData.comments.length
                : 0
        },
    },
})
